import uniRate from '../../components/uni-ui/uni-rate/uni-rate.vue'
export default {
components: {
 uniRate
},
props: {},
data () {
  return {
	  banner:[],
	  hot:[],
	  trailer:[],
	  likes:[],
	  indicatorDots: true,
	  vertical: true,
	  autoplay: true,
	  interval: 3000,
    }
  },
  methods: {
    getData(){
		uni.request({
			url: `${this.$api}/index/carousel/list?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				this.banner=res.data.data
			},
			fail: () => {},
			complete: () => {}
		});
	},
	getHot(){
		uni.request({
			url: `${this.$api}/index/movie/hot?type=superhero&qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				this.hot=res.data.data
			},
			fail: () => {},
			complete: () => {}
		});
	},
	gettrailer(){
		uni.request({
			url: `${this.$api}/index/movie/hot?type=trailer&qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				this.trailer=res.data.data
			},
			fail: () => {},
			complete: () => {}
		});
	},
	getlikes(){
		uni.request({
			url: `${this.$api}/index/guessULike?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				console.log(res);
				this.likes=res.data.data
			},
			fail: () => {},
			complete: () => {}
		});
	},
	praise(){
		uni.showToast({
			title:"已点赞"
		})
	}
  },
  mounted () {
	  
  },
  onLoad () {
    this.getData()
	this.getHot()
	this.gettrailer()
	this.getlikes()
  },
  onPullDownRefresh() {
          console.log('refresh');
          this.getlikes()
		  setTimeout(function () {
		    uni.stopPullDownRefresh();
		  }, 1000);
  },
  filters: {

  },
  computed: {

  },
  watch: {

  },
  directives: {

  }
}
